const imageElem = document.querySelector(`ul.gallery`);

function imgItem ({preview, original, description}){

    return `
    <li class="gallery-item">
        <a class="gallery-link" href="${original}">
            <img
            class="gallery-image"
            src="${preview}"
            alt="${description}"
            />
        </a>
    </li>
`
}
function imgList (images){
    return images.map(imgItem).join(``)
}

const markup = imgList(images);
imageElem.innerHTML = markup;

const lightbox = new SimpleLightbox('.gallery-item a', {
  captionsData: 'alt',          
  captionDelay: 250,  
  overlayOpacity: 1,        
  animationSpeed: 250,         
  showCounter: true,          
  enableKeyboard: true,          
  closeText: '×',              
  nextText: '→',                 
  prevText: '←',                
  loop: true,                  
  nav: true,                    
  alertError: true,              
  alertErrorMessage: 'Помилка завантаження зображення'
});





